import React from 'react';
import {Grid, Typography} from '@mui/material';
import './header.style.scss';

import { useNavigate } from 'react-router-dom';

const Header = () => {
    const navigate = useNavigate();
    const user = JSON.parse(localStorage.getItem('currentUser'));

    const logout = () => {
        localStorage.removeItem('currentUser');

        navigate('/');
    }

    return (
        <Grid container item xs={12} className="header">
            <Grid container item xs={4} className="logo">
                <Typography variant="h5">B<span>OO</span>G<span>OO</span></Typography>
            </Grid>
            <Grid container item xs={4}>
                
            </Grid>
            <Grid container item xs={4} justifyContent={'flex-end'} style={{paddingRight:'30px',}}>
                {user && <Typography style={{cursor:'pointer'}} onClick={logout} color="black" variant="body1">خروج</Typography>}
            </Grid>
        </Grid>
    );
}

export default Header;