import React from 'react';
import { Grid, Typography, Avatar } from '@mui/material';
import './ChatBox.scss';


const ChatBox = ({ chatHistory, currentUser }) => {
    const chatContainerRef = React.useRef(null);

    React.useEffect(() => {
        scrollToBottom();
      }, [chatHistory]);
    
      const scrollToBottom = () => {
        if (chatContainerRef.current) {
          chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
        }
      };

  return (
    <div className="chat-box" ref={chatContainerRef}>
      {chatHistory.map((message, index) => (

        <>
        {message.senderId === currentUser ? 
        
          <Avatar alt={message.senderId} src="/images/user.png" sx={{ width: 40, height: 40 }} />
          :
          <Grid container item xs={12} justifyContent="flex-end" style={{paddingRight:'10px'}}>
            <Avatar alt={message.receiverId}  src="/images/user2.png" sx={{ width: 40, height: 40 }} />
          </Grid>
        }
        <div
          key={message._id}
          className={`chat-bubble ${
            message.senderId === currentUser ? 'sender' : 'receiver'
          }`}
        >
          <Typography variant='body2' 
          color={ message.senderId === currentUser ? 'pink' : 'darkBlue'}>
            @{message.senderId}
            </Typography>
          <Typography
            variant="body1"
            className={`message ${
              message.senderId === currentUser ? 'sender-message' : 'receiver-message'
            }`}
          > 
            {message.message}
          </Typography>
        </div>
        <Typography variant="caption" className="timestamp" 
        textAlign={message.senderId === currentUser ? 'left' : 'right'}>
            {new Date(message.timestamp).toLocaleTimeString()}
          </Typography>
        </>
      ))}
    </div>
  );
};

export default ChatBox;
