import React, { useEffect, useState } from 'react';
import { Grid, Typography } from '@mui/material';
import './userList.style.scss';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
import io from 'socket.io-client';
import {
  TextField,
  InputAdornment,
  IconButton,
  Avatar,
  Badge
} from '@mui/material';
import { styled } from '@mui/material/styles';
import { BiSolidSend } from "react-icons/bi";
import ChatBox from './chatBox.component';
import {useBreakpoints} from '../../utils/useBreakpoints';

const GreenStyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    backgroundColor:  '#44b700',
    color: '#44b700',
    boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
    '&::after': {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      borderRadius: '50%',
      animation: 'ripple 1.2s infinite ease-in-out',
      border: '1px solid currentColor',
      content: '""',
    },
  },
  '@keyframes ripple': {
    '0%': {
      transform: 'scale(.8)',
      opacity: 1,
    },
    '100%': {
      transform: 'scale(2.4)',
      opacity: 0,
    },
  },
}));

const RedStyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    backgroundColor:  'red',
    color: '#44b700',
    boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
    '&::after': {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      borderRadius: '50%',
      animation: 'ripple 1.2s infinite ease-in-out',
      border: '1px solid currentColor',
      content: '""',
    },
  },
  '@keyframes ripple': {
    '0%': {
      transform: 'scale(.8)',
      opacity: 1,
    },
    '100%': {
      transform: 'scale(2.4)',
      opacity: 0,
    },
  },
}));

const UserList = () => {
  const {isMobile,isTablet, isDesktop} = useBreakpoints();
  const [socket, setSocket] = useState(null);
  const [users, setUsers] = useState([]);
  const [onlineUsers, setOnlineUsers] = useState([]);
  const [chats, setChats] = useState([]);
  const [receiver, setreceiver] = useState(null);
  const navigate = useNavigate();
  const location = useLocation();
  const currentUser = location.state?.currentUser ||  JSON.parse(localStorage.getItem('currentUser'));;
  const [isSocket, runSocket] = useState(false);
  const [messageInput, setMessageInput] = useState('');


  useEffect(() => {
    fetchUsers();
    console.log('Connecting to server...');
    const newSocket = io('http://localhost:5222');

    newSocket.on('connect', () => {
      console.log('Connected to server');
    });

    newSocket.on('disconnect', () => {
      console.log('Disconnected from server');
    });

    newSocket.emit('user-connected', currentUser);
    newSocket.emit("set-socket-id",{ userId: currentUser._id});

    setSocket(newSocket);

    return () => {
      newSocket.disconnect();
    };
  }, []);

  // const [refreshChatHistory, setRefreshChatHistory] = useState(false);

  useEffect(() => {    
    // Socket listener for "chat-history" event
    if(socket){
      socket.on("chat-history", (data) => {
        console.log(data);
        setChats(data.chatHistory);
      });
  
    // Clean up the socket listener when the component unmounts
    return () => {
      socket.off("chat-history");
    };
    }

}, [isSocket,receiver]);

  useEffect(() => {
    if (socket) {
      socket.on("private-message", function(data) {
        let sender = data.sender;
        let message = data.message;
        let timestamp = new Date(data.timestamp).toLocaleTimeString();

        setChats(prevChat=>[...prevChat,{senderId:sender,message,timestamp:data.timestamp}])


      });

      socket.on("error", function(error) {
        console.error(error);
      });
    }
  }, [isSocket]);
  

  const handleSendMessage = () => {
    if (messageInput.trim().length > 0) {
  
      // Emit a 'private_message' event to the server
      socket.emit('private-message', {
        sender: currentUser.username,
        receiver: receiver.username,
        message: messageInput.trim(),
      });

    }
  
    setMessageInput('');
  };
  


  useEffect(() => {
    console.log('you call get chat history');

      if(socket){
        socket.emit("get-chat-history",{sender:currentUser.username,receiver: receiver.username})
        return () => {
          // socket.disconnect();
          console.log('disconnecting to server in get chat history...');
        };
      }


  }, [isSocket,receiver]);

  const fetchUsers = async () => {
    try {
      const response = await axios.get('http://localhost:5222/users');
      if (response.data === 'login') {
        navigate('/signin');
      }
      const userData = response.data;
      console.log(userData);
      const allUsers = userData.users.filter(user=>user?.username !== currentUser?.username);
      setUsers(allUsers);
      setOnlineUsers(userData.onlineUsers);
    } catch (err) {
      console.error(err);
    }
  };

  const chatting = (user) => {
    setreceiver(user);
    runSocket(true);
  };

  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      handleSendMessage();
    }
  };

  return (
    <Grid container item xs={12} className="userList">
      <Grid container item xs={6} md={3}>
        <Grid container item xs={12} style={{position:'fixed',maxWidth: isDesktop ? '25vw' : '30vw',minHeight:'50px',top:'50px',left:0}} className="currentUser">
        <Grid container item  xs={4} md={4} justifyContent={'center'} alignItems={'center'}>
        <GreenStyledBadge
          overlap="circular"
          anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
          variant="dot"
        >
          <Avatar alt={currentUser?.username} src="/images/user.png" sx={{ width: 40, height: 40 }} />
        </GreenStyledBadge>
        </Grid>
        <Grid container item  flexDirection={'column'} xs={8} md={8} justifyContent={'flex-start'} alignItems={'flex-start'}>
          <Typography variant={isDesktop ? "h6" : 'body1'}>{currentUser?.username}</Typography>
          <Typography variant="caption"> 
            <span style={{color:'yellow'}}>آنلاین</span>
          </Typography>
        </Grid>
        </Grid>
        <Grid container item xs={12} 
        justifyContent={'flex-start'} 
        alignItems={'stretch'} 
        style={{position:'fixed',maxWidth: isDesktop ? '25vw' : '30vw',minHeight:'1000px',top:'100px',left:0}}>
          {/* Render the list of users */}
          <ul className="userList__list">
            {users.map((user,index) => (
              <li key={index} onClick={() => chatting(user)}>
                <Grid container item  xs={4} md={4} justifyContent={'center'} alignItems={'center'}>
                  {onlineUsers.includes(user?.username) ? 
                <GreenStyledBadge
                    overlap="circular"
                    anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                    variant="dot"
                  >
                    <Avatar alt={user?.username} src="/images/user.png" sx={{ width: 40, height: 40 }} />
                  </GreenStyledBadge>
                  :
                  <RedStyledBadge
                    overlap="circular"
                    anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                    variant="dot"
                  >
                    <Avatar alt={user?.username} src="/images/user.png" sx={{ width: 40, height: 40 }} />
                  </RedStyledBadge>
                  }
                </Grid>
                <Grid container item  flexDirection={'column'} xs={8} md={8} justifyContent={'flex-start'} alignItems={'flex-start'}>
                  <Typography variant={isDesktop ? "h6" : 'body1'}>{user?.username}</Typography>
                  <Typography variant="caption">
                    {onlineUsers.includes(user?.username) ? 
                    <span style={{color:'blue'}}>آنلاین</span> : <span style={{color:'gray'}}>آفلاین</span>}
                  </Typography>
                </Grid>
              </li>
            ))}
          </ul>
        </Grid>
      </Grid>
      <Grid container item xs={8} md={9} className="chatRoom" 
       style={{position:'fixed',maxWidth:'70vw',top:'15px',right:0}}>
          {chats.length ? 
          <ChatBox chatHistory={chats} currentUser={currentUser?.username} /> : 
          <Typography variant="body1">پیامی وجود ندارد</Typography>
        }
      </Grid>
      <Grid container item xs={6} md={3}></Grid>
      <Grid container item xs={6} md={9} className="textField" style={{ width:isDesktop ? '100%' : '65%'}}>
      <TextField
          id="message-input"
          className="messageInput"
          placeholder="Type your message here..."
          value={messageInput}
          disabled={receiver ? false : true}
          onChange={(e) => setMessageInput(e.target.value)}
          onKeyDown={handleKeyDown}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton  onClick={()=>handleSendMessage()}>
                  <BiSolidSend />
                </IconButton>
              </InputAdornment>
            ),
          }}
        />
      </Grid>
    </Grid>
  );
};

export default UserList;
