import React, { useState } from 'react';
import { TextField, Button, Typography, Link, Box } from '@mui/material';
import './signin.style.scss'; // SCSS file
import RtlProvider from '../../utils/rtlProvider';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const SignIn = () => {
  const [username, setUserName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isSignIn, setIsSignIn] = useState(true);
  const navigate = useNavigate();

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    if (name === 'email') {
      setEmail(value);
    } else if (name === 'password') {
      setPassword(value);
    }
    else if (name === 'username') {
      setUserName(value);
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    console.log('Username:', username);
    console.log('Email:', email);
    console.log('Password:', password);

    try {
      if (isSignIn) {
        // Login user
        const response = await axios.post('http://localhost:5222/login', { email, password });
        console.log(response.data); // Handle the login success response
        if (response.data.success) {
          const currentUser = response.data.user; // Assuming the user object is available in `response.data.user`
          localStorage.setItem('currentUser', JSON.stringify(currentUser));
          navigate('/users', { state: { currentUser } });
        }
      } else {
        // Register user
        const response = await axios.post('http://localhost:5222/register', { username, email, password });
        console.log(response.data); // Handle the registration success response
        if(response.data === 'success'){
          setIsSignIn(true);
        }
      }

      // Clear the form inputs
      setEmail('');
      setUserName('');
      setPassword('');
    } catch (error) {
      console.error(error); // Handle any error that occurred during the request
    }
  };

  return (
    <Box className="signin" style={{backgroundImage:"url('/images/login.png')"}}>
      <Typography variant="h2">{isSignIn ? 'ورود' : 'ثبت نام'}</Typography>
      <form onSubmit={handleSubmit}>
        {!isSignIn &&
          <RtlProvider>
          <TextField
            type="text"
            label="نام کاربری"
            name="username"
            value={username}
            onChange={handleInputChange}
            required
            fullWidth
            margin="normal"
          />
        </RtlProvider>
        }
        <RtlProvider>
          <TextField
            type="email"
            label="ایمیل"
            name="email"
            value={email}
            onChange={handleInputChange}
            required
            fullWidth
            margin="normal"
          />
        </RtlProvider>

        <RtlProvider>
          <TextField
            type="password"
            label="رمزعبور"
            name="password"
            value={password}
            onChange={handleInputChange}
            required
            fullWidth
            margin="normal"
          />
        </RtlProvider>

        <Button type="submit" variant="contained" color="primary">
          {isSignIn ? 'ورود' : 'ثبت نام'}
        </Button>
      </form>

      <Typography variant="body1" component="div" className="underLink">
        {isSignIn ? 'حساب کاربری ندارید؟ ' : 'قبلاً حساب کاربری داشته‌اید؟ '}
        <Link
          component="span"
          className="toggle-link"
          onClick={() => setIsSignIn(!isSignIn)}
        >
          {isSignIn ? 'ثبت نام' : 'ورود'}
        </Link>
      </Typography>
    </Box>
  );
};

export default SignIn;
